import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Container } from '@material-ui/core';
import { useFoodStateValue } from '../../context/food-state';
import { MenuCard } from '../../compontents/MenuCard';
import { LottieAnimation } from '../../compontents/LottieAnimation';

export const Menu = () => {
  const { foodState, setFoodState } = useFoodStateValue();

  return (
    <Container className='menu'>
      {foodState.length === 0 ? (
        <div className='menu__default'>
          <LottieAnimation url='./images/animations/empty-bag4.json' />
          <h3>PRÁZDNO</h3>
        </div>
      ) : (
        foodState.map(({ title, category, grammage, nutrition, imgSrc }) => {
          return (
            <MenuCard
              setFoodState={setFoodState}
              title={title}
              category={category}
              grammage={grammage}
              nutrition={nutrition}
              imgSrc={imgSrc}
            />
          );
        })
      )}
    </Container>
  );
};
