import React, { useState, useEffect } from 'react';
import { useFoodStateValue } from '../context/food-state';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import InputLabel from '@material-ui/core/InputLabel';
import Input from '@material-ui/core/Input';
import FormControl from '@material-ui/core/FormControl';
import TextField from '@material-ui/core/TextField';
import Select from '@material-ui/core/Select';
import InputAdornment from '@material-ui/core/InputAdornment';
import clsx from 'clsx';

const useStyles = makeStyles((theme) => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  marginTop: {
    marginTop: '2rem',
  },
}));

export function GrammageModal({ title, change, setChange }) {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [grammage, setGrammage] = useState('');
  const [grammageUnit, setGrammageUnit] = useState('Gramy');

  const { foodState, setFoodSate } = useFoodStateValue();

  const handleChange = (event) => {
    setGrammageUnit(event.target.value || '');
    console.log('target value', event.target.value);
  };

  const handleClickOpen = () => {
    setOpen(true);
  };

  //! renders a new array of all the values => i want only one value (find?)
  const getFoodGrammageValue = () => {
    let currentFood;

    foodState.find((food) => {
      if (food.title === title) {
        currentFood = food.grammage.value;
      }
    });

    return currentFood;
  };

  // const GRAMMAGE_VALUE =
  //   getFoodGrammageValue() === 0 ? grammage : getFoodGrammageValue();
  // const GRAMMAGE_VALUE = getFoodGrammageValue()  : grammage;

  const handleClose = () => {
    setOpen(false);
    setChange(!change);

    foodState.forEach((food) => {
      if (food.title === title) {
        food.grammage.value = Number(grammage);

        food.grammage.unit = getGrammageUnit();
      }
    });
    console.log(foodState);
  };

  const setInputFilter = (textbox, inputFilter) => {
    console.log(textbox);
    [
      'input',
      'keydown',
      'keyup',
      'mousedown',
      'mouseup',
      'select',
      'contextmenu',
      'drop',
    ].forEach(function (event) {
      textbox.addEventListener(event, function () {
        if (inputFilter(this.value)) {
          this.oldValue = this.value;
          this.oldSelectionStart = this.selectionStart;
          this.oldSelectionEnd = this.selectionEnd;
        } else if (this.hasOwnProperty('oldValue')) {
          this.value = this.oldValue;
          this.setSelectionRange(this.oldSelectionStart, this.oldSelectionEnd);
        } else {
          this.value = '';
        }
      });
    });
  };

  const getGrammageUnit = () => {
    return grammageUnit === 'Kilogramy'
      ? 'kg'
      : grammageUnit === 'Gramy'
      ? 'g'
      : grammageUnit === 'Kus'
      ? 'ks'
      : '';
  };

  useEffect(() => {
    console.log('grammage', grammage);
    console.log('foodState', foodState);

    // console.log('Grammage Value', GRAMMAGE_VALUE);
    console.log('function', getFoodGrammageValue());

    if (getFoodGrammageValue() !== 0) {
      console.log('the food state has a value!');
      setGrammage(getFoodGrammageValue());
    }
  }, [open]);

  return (
    <div>
      <Button variant='contained' color='primary' onClick={handleClickOpen}>
        Upravit
      </Button>
      <Dialog
        disableBackdropClick
        disableEscapeKeyDown
        open={open}
        onClose={handleClose}
      >
        <DialogTitle>{title}</DialogTitle>
        <DialogContent>
          <form className={classes.container}>
            <FormControl className={classes.formControl}>
              <InputLabel htmlFor='demo-dialog-native'>Jednotka</InputLabel>
              <Select
                native
                value={grammageUnit}
                onChange={handleChange}
                input={<Input id='demo-dialog-native' />}
              >
                <option value='Kilogramy'>Kilogramy</option>
                <option value='Gramy'>Gramy</option>
                <option value='Kus'>Kus</option>
              </Select>
            </FormControl>
            <TextField
              onClick={() =>
                setInputFilter(
                  document.getElementById('outlined-start-adornment'),
                  function (value) {
                    return /^-?\d*$/.test(value);
                  }
                )
              }
              onInput={(e) => {
                setGrammage(e.target.value);
              }}
              label='With normal TextField'
              id='outlined-start-adornment'
              className={clsx(classes.marginTop, classes.textField)}
              InputProps={{
                startAdornment: (
                  <InputAdornment position='start'>
                    {getGrammageUnit()}
                  </InputAdornment>
                ),
              }}
              value={grammage}
              variant='outlined'
            />
          </form>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color='primary'>
            Cancel
          </Button>
          <Button onClick={handleClose} color='primary'>
            Ok
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
