import React, { useState, useEffect } from 'react';
import './App.css';
import { Container, Button, Link } from '@material-ui/core';
import { BottomNav } from './compontents/layout/BottomNav';
import { Header } from './compontents/layout/Header';
import { LottieAnimation } from './compontents/LottieAnimation';
import { CreateRecipe, MyRecipes, MyProfile } from './pages/main';
import { Switch, Route, useLocation, useHistory } from 'react-router-dom';

function App() {
  const [showIntro, setShowIntro] = useState(true);
  const location = useLocation();
  const history = useHistory();

  useEffect(() => {
    location.pathname !== '/' ? setShowIntro(false) : setShowIntro(true);
  }, [location.pathname]);

  return (
    <div className='app'>
      <Header />
      {/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
      <Container className='app__content'>
        <Switch>
          <Route path='/my-profile' exact>
            <MyProfile />
          </Route>
          <Route path='/create-recipe'>
            <CreateRecipe />
          </Route>
          <Route path='/my-recipes'>
            <MyRecipes />
          </Route>
        </Switch>
      </Container>

      {showIntro && (
        <div className='intro-page'>
          <LottieAnimation url='./images/animations/carrot-rocket.json' />
          <h2 className='intro-page__title'>Vítej Na Cholify!</h2>
          <Button
            variant='contained'
            color='primary'
            onClick={() => history.push('/create-recipe')}
          >
            Vytvořit Recept
          </Button>
        </div>
      )}

      <BottomNav />
    </div>
  );
}

export default App;
