import React from 'react';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import { useFoodStateValue } from '../context/food-state';

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: '#43a047',
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const useStyles = makeStyles({
  table: {
    maxWidth: 389,
  },
  tableRow: {
    display: 'flex',
    flexDirection: 'column',
    width: '100%',
  },
  tableColumn: {
    flex: 1,
  },
  upperCase: {
    fontWeight: 'bold',
    textTransform: 'uppercase',
  },
  paddingVertical: {
    marginTop: '2rem',
    marginBottom: '2rem',
  },
});

export const NutritionTable = () => {
  const classes = useStyles();

  const { foodState, setFoodState } = useFoodStateValue();

  //! Helpers

  function createData(title, myData, unit) {
    return { title, myData, unit };
  }

  const calculateValue = (nutritionType) => {
    let totalValue = 0;
    let currentValue = 0;

    if (foodState.length > 0) {
      foodState.forEach(({ grammage, nutrition }) => {
        const grammageMultiplier = grammage.value / 100;

        console.log('grammage', grammage);

        currentValue = nutrition[nutritionType] * grammageMultiplier;
        totalValue = totalValue + currentValue;
      });

      return totalValue.toFixed(1);
    }
  };

  const rows = [
    createData('Kalorie', calculateValue('calories'), 'g'),
    createData('Proteiny', calculateValue('protein'), 'g'),
    createData('Sacharidy', calculateValue('carbohydrate'), 'g'),
    createData('Cukry', calculateValue('sugar'), 'g'),
    createData('Celkové Tuky', calculateValue('totalFat'), 'g'),
    createData('Nasycené Mastné Kyseliny', calculateValue('saturatedFat'), 'g'),
    createData(
      'Trans Mastné Kyseliny',
      calculateValue('monosaturatedFat'),
      'g'
    ),
    createData('Polynenasycené', calculateValue('polysaturatedFat'), 'g'),
    createData('Cholesterol', calculateValue('cholesterol'), 'mg'),
    createData('Vláknina', calculateValue('fiber'), 'g'),
    createData('Železo', calculateValue('iron'), 'mg'),
    createData('Sůl', calculateValue('sodium'), 'mg'),
    createData('Vápník', calculateValue('calcium'), 'gm'),
    createData('PHE', calculateValue('phe'), ''),
  ];

  return (
    <TableContainer className={classes.paddingVertical} component={Paper}>
      <Table className={classes.table} aria-label='customized table'>
        <TableHead>
          <TableRow>
            <StyledTableCell className={classes.upperCase} align='left'>
              Moje Živiny
            </StyledTableCell>
            <StyledTableCell align='right'></StyledTableCell>
            <StyledTableCell align='right'></StyledTableCell>
            <StyledTableCell align='right'></StyledTableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <StyledTableRow key={row.name}>
              <StyledTableCell component='th' scope='row'>
                {row.title}
              </StyledTableCell>
              <StyledTableCell align='right'></StyledTableCell>
              <StyledTableCell align='right'>{row.myData}</StyledTableCell>
              <StyledTableCell align='right'>{row.unit}</StyledTableCell>
            </StyledTableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
};
